import { isBasePayload } from './BasePayload';
export function isQredoConnectPayload(payload, method) {
    return (isBasePayload(payload) &&
        payload.type === 'qredo-connect' &&
        'method' in payload &&
        payload.method === method &&
        'args' in payload &&
        !!payload.args);
}
