import { filter, fromEvent, map, share } from 'rxjs';
export class WindowMessageStream {
    constructor(name, target) {
        if (name === target) {
            throw new Error('[WindowMessageStream] name and target must be different');
        }
        this._name = name;
        this._target = target;
        this.messages = fromEvent(window, 'message').pipe(filter((message) => message.source === window && message.data.target === this._name), map((message) => message.data.payload), share());
    }
    send(payload) {
        const msg = {
            target: this._target,
            payload,
        };
        window.postMessage(msg);
    }
}
