import { toBase64 } from '@mysten/bcs';
import { parse } from 'valibot';
import { normalizeSuiObjectId } from '../utils/sui-types.js';
import { Argument } from './data/internal.js';
// Keep in sync with constants in
// crates/sui-framework/packages/sui-framework/sources/package.move
export var UpgradePolicy;
(function (UpgradePolicy) {
    UpgradePolicy[UpgradePolicy["COMPATIBLE"] = 0] = "COMPATIBLE";
    UpgradePolicy[UpgradePolicy["ADDITIVE"] = 128] = "ADDITIVE";
    UpgradePolicy[UpgradePolicy["DEP_ONLY"] = 192] = "DEP_ONLY";
})(UpgradePolicy || (UpgradePolicy = {}));
/**
 * Simple helpers used to construct transactions:
 */
export const Commands = {
    MoveCall(input) {
        const [pkg, mod = '', fn = ''] = 'target' in input ? input.target.split('::') : [input.package, input.module, input.function];
        return {
            $kind: 'MoveCall',
            MoveCall: {
                package: pkg,
                module: mod,
                function: fn,
                typeArguments: input.typeArguments ?? [],
                arguments: input.arguments ?? [],
            },
        };
    },
    TransferObjects(objects, address) {
        return {
            $kind: 'TransferObjects',
            TransferObjects: {
                objects: objects.map((o) => parse(Argument, o)),
                address: parse(Argument, address),
            },
        };
    },
    SplitCoins(coin, amounts) {
        return {
            $kind: 'SplitCoins',
            SplitCoins: {
                coin: parse(Argument, coin),
                amounts: amounts.map((o) => parse(Argument, o)),
            },
        };
    },
    MergeCoins(destination, sources) {
        return {
            $kind: 'MergeCoins',
            MergeCoins: {
                destination: parse(Argument, destination),
                sources: sources.map((o) => parse(Argument, o)),
            },
        };
    },
    Publish({ modules, dependencies, }) {
        return {
            $kind: 'Publish',
            Publish: {
                modules: modules.map((module) => typeof module === 'string' ? module : toBase64(new Uint8Array(module))),
                dependencies: dependencies.map((dep) => normalizeSuiObjectId(dep)),
            },
        };
    },
    Upgrade({ modules, dependencies, package: packageId, ticket, }) {
        return {
            $kind: 'Upgrade',
            Upgrade: {
                modules: modules.map((module) => typeof module === 'string' ? module : toBase64(new Uint8Array(module))),
                dependencies: dependencies.map((dep) => normalizeSuiObjectId(dep)),
                package: packageId,
                ticket: parse(Argument, ticket),
            },
        };
    },
    MakeMoveVec({ type, elements, }) {
        return {
            $kind: 'MakeMoveVec',
            MakeMoveVec: {
                type: type ?? null,
                elements: elements.map((o) => parse(Argument, o)),
            },
        };
    },
    Intent({ name, inputs = {}, data = {}, }) {
        return {
            $kind: '$Intent',
            $Intent: {
                name,
                inputs: Object.fromEntries(Object.entries(inputs).map(([key, value]) => [
                    key,
                    Array.isArray(value) ? value.map((o) => parse(Argument, o)) : parse(Argument, value),
                ])),
                data,
            },
        };
    },
};
